.form-container {  
  display: flex;
  justify-content: center;
  align-items: center;
}

.form-input-container {  
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-right: 10px;
}

.input-add-todo {
  box-sizing: border-box;
  font-size: 18px;
  width: 100%;
  height: 40px;
  padding: 5px;
  border-radius: 5px;
  border: 0;
  margin-bottom: 10px;
}

.input-add-todo:focus {
  border: 1px solid #4b0979;
}

.input-duedate-todo {
  box-sizing: border-box;
  font-size: 18px;
  width: 100%;
  height: 40px;
  padding: 5px;
  border-radius: 5px;
  border: 0;
  margin-bottom: 10px;
}

.input-duedate-todo:focus {
  border: 1px solid #4b0979;
}

.btn-add-todo {  
  background-color: #198754;
  color: white;
  cursor: pointer;
  font-size: 18px;
  width: 30%;
  height: 90px;
  padding: 3px;
  border-radius: 5px;
  border: 0;
  margin-bottom: 10px;
}

.btn-add-todo:hover {
  opacity: 0.6;
}