.filter-btn-group {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 20px;    
  }

  .btn-filter-todo {    
    cursor: pointer;
    border: 0;
    font-size: 20px;
    border-radius: 5px;
    margin:2%;
    color: white;
    width: 33%;
    height: 34px;
    background-color: #3c0d5d;
  }
  
  .btn-filter-todo:hover {
    opacity: 0.6;
    border: 1px solid white;
  }
  
  .btn-filter-todo[aria-pressed="true"] {
    text-decoration: underline;
    border-color: white;
  }