@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 2%,
    rgba(60, 13, 93, 1) 81%
  );  
  font-family: 'Ubuntu', sans-serif;
}

.Wrapper {  
  margin: 20px auto;
  width: 40%;
  padding: 10px 100px 10px 100px;  
  border-radius: 15px;
  opacity: 70%;
  background-color: #121b23;
  color: white;
}

h1 {
  text-align: center;
}

h2 {
  text-align: center;
}